ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.page {
  height: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
  margin: 0;
  min-width: 320px;
}
.page__body {
  min-width: 320px;
  box-sizing: border-box;
  min-height: 100%;
  background-color: #ffffff;
  color: #000000;
  margin: 0 auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-header__wrapper {
  padding: 10px 20px 17px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}
.main-header__nav {
  display: block;
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.6;
}
.main-header__logo-cat-energy:hover {
  opacity: 0.8;
}
.main-header__logo-cat-energy:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    padding: 0 30px;
    align-items: flex-end;
  }
  .main-header__logo-cat-energy {
    display: none;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-header__nav {
    display: block;
  }
}
@media (min-width: 1440px) {
  .main-header__wrapper {
    max-width: 1440px;
    margin-left: auto;
    margin-right: auto;
    padding: 53px 110px 0 110px;
    align-items: center;
  }
}
.main-nav {
  width: auto;
}
.main-nav__list {
  position: fixed;
  z-index: 4;
  left: 0;
  right: 0;
  top: 65px;
  width: 100%;
  background-color: #ffffff;
}
.site-list__item {
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
}
.site-list__link {
  padding-top: 22px;
  padding-bottom: 24px;
  padding-left: 0;
  position: relative;
  display: block;
  font-family: inherit;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  text-align: center;
}
.site-list__link:hover {
  opacity: 0.6;
}
.site-list__link:active {
  opacity: 0.3;
}
.main-nav__burger {
  fill: #000000;
  width: 24px;
  height: 18px;
  stroke: #000000;
  stroke-width: 1.5;
}
.main-nav__toggle {
  display: block;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
  padding: 0;
  width: 24px;
  height: 16px;
}
.main-header__logo-cat-energy {
  margin-left: -8px;
  margin-top: 6px;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 20px;
  left: 25px;
  height: 66px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__burger {
  display: none;
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 28px;
  right: 20px;
  width: 25px;
  height: 1.5px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__list {
    position: absolute;
    min-width: 320px;
  }
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__wrapper {
    display: block;
  }
  .main-nav {
    min-width: 440px;
    height: 36px;
  }
  .main-nav__list {
    position: static;
    display: flex;
    justify-content: space-between;
  }
  .site-list__item {
    width: auto;
    border: none;
  }
  .site-list__link {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    min-height: auto;
    text-align: center;
    padding: 0;
  }
  .site-list__link--active {
    position: relative;
  }
  .site-list__link--active::before {
    position: absolute;
    content: "";
    width: 100%;
    background-color: #68b738;
    height: 2px;
    bottom: -9px;
  }
  .main-nav__toggle {
    display: none;
  }
}
