    padding-left: 19px;
  }
  .additionally__input[type="checkbox"]:checked + .additionally__mark {
    background-image: url("../img/tick.svg");
  }
}
@media (min-width: 1440px) {
  .additionally__item {
    margin-bottom: 0;
  }
  .additionally__list {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 80px;
    padding: 56px 0 32px;
  }
}
.line::after {
  content: "";
  position: absolute;
  top: 16px;
  width: 300%;
  height: 2px;
  background-color: #68b738;
}
.line {
  position: relative;
}
@media (min-width: 768px) {
  .line::after {
    top: 23px;
  }
}
